.about-contnet{
    width: 100%;
    margin: 3rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
}

.about-contnet .left{
    text-align: center;
    margin: auto;
    padding: 1rem;
    max-width: 350px;

}

.about-contnet .left p{
    margin: 1.2rem 0;

}
img{
    width: 300px;
    height: 400px;
}
.about-contnet .right {
    max-width: 700px;
}
.right .img-container{
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    position: relative;
    align-items: center;
    text-align: center;

}

.right .img{
    max-width: 90%;
    border: 1px solid #333;

}

.right .top{
    grid-column: 1/ span 8;
    grid-row: 1;
    padding-top: 20%;
    z-index: 6;
}
.right .bottom{
    grid-column: 4 / -1;
    grid-row: 1;
    z-index: 5;
}